import React from 'react';
import classes from './map_weapons.module.css'
import {Col, Container, Row} from "react-bootstrap";
import {useDispatch, useSelector} from "react-redux";
import {switchToHeavy, switchToNades, switchToPistol, switchToRifle, switchToSmg} from "../actions/actions";
import Weapon from "../Weapon/Weapon";
import 'bootstrap/dist/css/bootstrap.min.css';

function MapWeapons(){
    const weapons = useSelector(state => state.weapons);
    const weaponType = useSelector(state => state.weaponType);
    const dispatch = useDispatch();

    return(
        <section className={classes.mapWeapons}>
            <Container>
                <Row>
                    <div className={`${classes.weaponType} col-md-12`}>
                        <span onClick={() => dispatch(switchToRifle())} className={weaponType === 'rifle' ? classes.active : null}>Rifles</span>
                        <span onClick={() => dispatch(switchToSmg())} className={weaponType === 'smg' ? classes.active : null}>SMG's</span>
                        <span onClick={() => dispatch(switchToHeavy())} className={weaponType === 'heavy' ? classes.active : null}>Heavy</span>
                        <span onClick={() => dispatch(switchToPistol())} className={weaponType === 'pistol' ? classes.active : null}>Pistols</span>
                        <span onClick={() => dispatch(switchToNades())} className={weaponType === 'nades' ? classes.active : null}>Grenades and Knife</span>
                    </div>
                    <Col className={classes.weapons}>
                        {
                            weapons.map((weapon, index) => {
                                if(weapon.type === weaponType){
                                    return(
                                        <Weapon name={weapon.name} img={weapon.img} kills={weapon.kills} accuracy={weapon.accuracy} key={index + weapon.name}/>
                                    )
                                }
                                else{
                                    return null
                                }
                            })
                        }
                    </Col>
                </Row>
            </Container>
        </section>
    )

}


export default MapWeapons;