import React from 'react';
import {Col, Container, Row} from "react-bootstrap";
import classes from './footer.module.css';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faInstagram, faTwitter, faVk} from "@fortawesome/free-brands-svg-icons";

function Footer() {
    return (
        <footer className={classes.footer}>
            <Container fluid>
                <Row className={classes.row}>
                    <Col md={{span: 3, offset: 0}}>
                        <div className={classes.logo}>
                            <img src={require('../images/cybershoke-text.svg')} alt="logo"/>
                        </div>
                    </Col>
                    <Col md={6}>
                        <ul>
                            <li><a href="https://cybershoke.net/home"><span>играть</span></a></li>
                            <li><a href="https://cybershoke.net/premium"><span>premium</span></a></li>
                            <li><a href="https://cybershoke.net/shop"><span>магазин</span></a></li>
                            <li><a href="https://cybershoke.net/competitions"><span>соревнования</span></a></li>
                            <li><a href="https://cybershoke.net/contacts"><span>контакты</span></a></li>
                            <li><a href="https://cybershoke.net/learn"><span>помощь</span></a></li>
                        </ul>
                    </Col>
                    <Col md={{span: 3, offset: 0}} className={classes.social_icons}>
                        <div className={`${classes.circle} ${classes.vk}`}>
                            <FontAwesomeIcon icon={faVk}/>
                        </div>
                        <div className={`${classes.circle} ${classes.twitter}`}>
                            <FontAwesomeIcon icon={faTwitter}/>
                        </div>
                        <div className={`${classes.circle} ${classes.instagram}`}>
                            <FontAwesomeIcon icon={faInstagram}/>
                        </div>
                    </Col>
                </Row>
            </Container>
        </footer>
    )

};


export default Footer;