import React from 'react';
import classes from './weapon.module.css'
import {Col} from "react-bootstrap";


function Weapon(props){
    return(
        <Col md={8} className={classes.weapon} key={props.key}>
            <div className={classes.weapon_image}>
                <img src={props.img} alt={props.name}/>
                <span>{props.name}</span>
            </div>
            <div className={classes.weapon_stats}>
                <p>Kills: <span>{props.kills}</span></p>
                <p>Accuracy: <span>{props.accuracy}</span></p>
            </div>
        </Col>
    )

};


export default Weapon;