export const switchToRifle = () => {
    return {
        type: 'RIFLE',
        payload: 'rifle'
    }
};
export const switchToPistol = () => {
    return {
        type: 'PISTOL',
        payload: 'pistol'
    }
};
export const switchToSmg = () => {
    return {
        type: 'SMG',
        payload: 'smg'
    }
};
export const switchToHeavy = () => {
    return {
        type: 'HEAVY',
        payload: 'heavy'
    }
};
export const switchToNades = () => {
    return {
        type: 'NADES',
        payload: 'nades'
    }
};
