import React, {useState} from "react";
import {Col, Container, Row} from "react-bootstrap";
import classes from './MainStatistics.module.css'
import {Switch, Route, NavLink} from 'react-router-dom';
import {useSelector} from "react-redux";
import Routes from "../Routes/Routes";
import Statistics from "../Statistics/Statistics";
import Inventory from "../Inventory/Inventory";
import Profile from "../Profile/Profile";
import Transactions from "../Transactions/Transactions";
import Sponsors from "../Sponsors/Sponsors";
import RealTimeData from "../RealTimeData/RealTimeData";


function MainStatistics() {

    const [getLevel, setLevel] = useState(10);

    const current_xp = `${getLevel - 3}%`;
    const needed_xp = `${getLevel}%`;


    return (

        <section id='main_statistics' className={classes.main_statistics}>
            <Container fluid className={classes.test}>
                <Row className={classes.upper_statistics}>
                    <div className={classes.user_pic}>
                        <img src={require('../images/ace_ventura_1.jpg')} alt="user_picture"/>
                    </div>
                    <Col md={6}>
                        <Row className={classes.progress}>
                            <span>Username</span>
                            <span>Last seen 10 days ago</span>
                        </Row>
                        <Row className={classes.level}>
                            <span className={classes.current_xp} style={{left: current_xp}}>750 XP</span>
                            <div className={classes.progress_bar}><span style={{width: `${getLevel}%`}}></span></div>
                            <span className={classes.needed_xp} style={{left: needed_xp}}>1000 XP</span>
                            <div className={classes.for_level}>
                                <img src={require('../images/level.svg')} alt="level"/>
                            </div>
                        </Row>
                    </Col>
                </Row>
            </Container>
            <Routes/>
            <Switch>
                <Route path='/' exact component={Statistics}/>
                <Route path='/profile' component={Profile}/>
                <Route path='/inventory' exact component={Inventory}/>
                <Route path='/transactions' component={Transactions}/>
                {/*<Route path='/tasks' component={Tasks}/>*/}
                {/*<Route path='/achivments' component={Achivments}/>*/}
                {/*<Route path='/friends' component={Friends}/>*/}
            </Switch>
            <Sponsors/>
        </section>
    )
}

export default MainStatistics;