import {combineReducers} from "redux";
import routeReducer from "./routeReducer";
import lightStatsReducer from "./lightStatReducer";
import transactionsReducer from "./transactionsReducer";
import mapStatisticsReducer from "./mapStatistics";
import mapWeaponsReducer from './mapWeapons'
import weaponTypeReducer from "./weaponType";

const rootReducer = combineReducers({
    routes: routeReducer,
    lightStats: lightStatsReducer,
    transactions: transactionsReducer,
    maps: mapStatisticsReducer,
    weapons: mapWeaponsReducer,
    weaponType: weaponTypeReducer,
});

export default rootReducer;