import React from "react";
import {Col, Container, Row} from "react-bootstrap";
import classes from './sponsors.module.css'


function Sponsors() {
    return(
        <Container fluid>
            <Row className={classes.sponsors}>
                <Col md={2}><a href="https://hellstore.net/#/coinflip" target='_blank' rel="noopener noreferrer"><img src={require('../images/hellstore.webp')} alt=""/></a></Col>
                <Col md={2}><a href="https://cs.fail/" target='_blank' rel="noopener noreferrer"><img src={require('../images/csfail.webp')} alt=""/></a></Col>
                <Col md={2}><a href="https://ggdrop.wtf/" target='_blank' rel="noopener noreferrer"><img src={require('../images/ggdrop.webp')} alt=""/></a></Col>
                <Col md={2}><a href="https://pro.logitech.promo/shoke/?tw-shoke-from-cybershoke" target='_blank' rel="noopener noreferrer"><img src={require('../images/logitech.webp')} alt=""/></a></Col>
            </Row>
        </Container>
    )
}

export default Sponsors;