import React from 'react';
import {Container, Row} from "react-bootstrap";
import classes from './footerInfo.module.css';


function footerInfo() {
    return (
        <div>
            <Container fluid>
                <Row className={classes.row}>
                    <span>Размещенная на настоящем сайте информация носит исключительно информационный характер и ни при каких условиях не является публичной офертой.</span>
                    <a href="https://cybershoke.net/policy">Политика конфиденциальности</a>
                    <div className={classes.payment}>
                        <div className={classes.mastercard}><img src={require('../images/mastercard.webp')} alt=""/></div>
                        <div className={classes.visa}><img src={require('../images/visa.png')} alt=""/></div>
                    </div>
                </Row>
            </Container>
        </div>
    )

};


export default footerInfo;