const initialState = 'rifle';

const weaponTypeReducer = (state = initialState, action) => {
    switch (action.type) {
        default:
            return state;
        case 'RIFLE':
            return action.payload;
        case 'PISTOL':
            return action.payload;
        case 'SMG':
            return action.payload;
        case 'HEAVY':
            return action.payload;
        case 'NADES':
            return action.payload;

    }
};

export default weaponTypeReducer;