import React from "react";
import classes from './Profile.module.css'
import {Container, Row} from "react-bootstrap";
import Input from "../Input/Input";

function Profile() {
    return (
        <section className={classes.section}>
            <Container fluid>
                <Row className={classes.inputs}>
                    <Input id='tradeLink' labelText='Your trade link' placeholder='Enter your trade link' buttonText='Save' help={true}/>
                    <Input id='promoCode' labelText='Promocode' placeholder='Enter your promocode' buttonText='Send'/>
                </Row>
            </Container>
        </section>
    )
}


export default Profile;