import React from 'react';
import classes from './mapStatistics.module.css';
import {Col, Container, Row} from "react-bootstrap";
import {useSelector} from "react-redux";
import 'bootstrap/dist/css/bootstrap.min.css';

function MapStatistics() {
    const maps = useSelector(state => state.maps);


    return (
        <section className={classes.maps}>
            <Container fluid>
                <Row className={classes.row}>
                    {
                        maps.map((map, index) => {

                            return (
                                <div className={`${classes.map} col-md-3`} style={{backgroundImage: `url(${map.backg})`}}>
                                    <Col className={classes.map_logo}>
                                        <div className={classes.front}>
                                            <img src={map.logo} alt={map.name}/>
                                        </div>
                                    </Col>
                                    <Col className={classes.stats}>
                                        <span className={classes.span}>{map.name}</span>
                                        <span className={classes.span_container}><span>Total games: </span><span className={classes.games}>{map.games}</span></span>
                                        <span className={classes.span_container}><span>Winrate: </span><span className={map.winrate > 49 ? classes.winrate_green : classes.winrate_red}>{map.winrate}%</span></span>
                                    </Col>

                                </div>
                            )
                        })
                    }
                </Row>
            </Container>
        </section>
    )
};


export default MapStatistics;