import React from "react";
import MainStatistics from "../MainStatistics/MainStatistics";
import classes from './Main.module.css'
import RealTimeData from "../RealTimeData/RealTimeData";
import Footer from "../Footer/Footer";
import FooterInfo from "../FooterInfo/FooterInfo";

function Main() {
    return (
        <section id='main' className={classes.main}>
            <MainStatistics/>
            <div className={classes.line}>
                <div><img src={require('../images/cybershoke.svg')} alt="logo"/></div>
                <h2>CYBERSHOKE.NET - крупнейшая тренировочно-развлекательная сетка серверов для CS:GO в мире!</h2>
                <div><img src={require('../images/cybershoke.svg')} alt="logo"/></div>
            </div>
            <div className={classes.realtime_data}>
                <RealTimeData data={213124} name='игроков всего'/>
                <RealTimeData data={48934} name='игроков в сутки'/>
                <RealTimeData data={4533} name='новых игроков'/>
                <RealTimeData data={678} name='кол-во серверов'/>
                <RealTimeData data={26} name='кол-во режимов'/>
            </div>
            <Footer/>
            <FooterInfo/>
        </section>
    )
}

export default Main;