import React from "react";
import classes from './input.module.css'
import {Col, Row} from "react-bootstrap";
import Button from "../Button/Button";

function Input(props) {
    return (
        <Col md={6}>
            <form className={classes.form}>
                <div className={classes.form_block}>
                    <label htmlFor={props.id}>{props.labelText}</label>
                    {props.help ? <a href="https://store.steampowered.com/" target='_blank'>How do I get my trade URL?</a> : null}
                </div>
                <div className={classes.bottom_formGroup}>
                    <input type="text" name={props.id} id={props.id} placeholder={props.placeholder}/>
                    <Button text={props.buttonText}/>
                </div>
            </form>
        </Col>
    )
}

export default Input;