import React from "react";
import classes from './statistics.module.css'
import {useSelector} from "react-redux";
import {Col, Container, Row} from "react-bootstrap";
import MapStatistics from "../MapStatistics/MapStatistics";
import MapWeapons from "../MapWeapons/MapWeapons";

function Statistics() {

    const lightStats = useSelector(state => state.lightStats);

    return(
        <section className={classes.row_of_stats}>
            <Container fluid>
                <Row className={classes.stats}>
                    {
                        lightStats.map((stat, index) => {
                            if(stat.img){
                                return (
                                    <Col key={stat.name + index} className={classes.country}>
                                        <div>
                                            <img src={require('../images/ua_flag.png')} alt="country"/>
                                        </div>
                                        <span className={classes.span}>{stat.name}</span>
                            </Col>
                                )
                            }
                            else{
                                return(
                                    <Col key={stat.name + index} className={classes.value}>
                                        <span className={classes.bold}>{stat.payload}</span>
                                        <span className={classes.span}>{stat.name}</span>
                                    </Col>
                                )
                            }
                        })
                    }
                </Row>
            </Container>
            <MapStatistics/>
            <MapWeapons/>
        </section>
    )
}

export default Statistics;