import React from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import classes from './Header.module.css'
import {Col, Row, Container} from "react-bootstrap";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faSignOutAlt} from "@fortawesome/free-solid-svg-icons";
import {faInstagram, faTwitter, faVk} from "@fortawesome/free-brands-svg-icons";
import Button from "../Button/Button";

function Header() {
    return (
        <section id='header' className={classes.header_section}>
            <Container fluid className={classes.header}>
                <header>
                    <Row className={classes.row}>
                        <Col className={classes.leftside}>
                            <span>следи за нами в соц сетях</span>
                            <div className={`${classes.circle} ${classes.vk}`}>
                                <FontAwesomeIcon icon={faVk}/>
                            </div>
                            <div className={`${classes.circle} ${classes.twitter}`}>
                                <FontAwesomeIcon icon={faTwitter}/>
                            </div>
                            <div className={`${classes.circle} ${classes.instagram}`}>
                                <FontAwesomeIcon icon={faInstagram}/>
                            </div>
                        </Col>
                        <Col className={classes.brand_logo}>
                            <img src={require('../images/cybershoke-text.svg')} alt="logo"/>
                        </Col>
                        <Col className={classes.rightside}>
                            <span>1000</span>
                            <div className={classes.diamond}>
                                <img src={require('../images/diamond.svg')} alt="diamond"/>
                            </div>
                            <div>
                                <Button text='Пополнить'/>
                            </div>
                            <div className={classes.userpic}>
                                <img src={require('../images/ace_ventura_1.jpg')} alt="userpic"/>
                            </div>

                            <FontAwesomeIcon icon={faSignOutAlt} className={classes.sign_out}/>
                        </Col>

                    </Row>
                </header>
            </Container>
        </section>
    )
}

export default Header;